import React, { useContext } from 'react'
import {Formik, Form, Field, ErrorMessage } from 'formik'
import { AtmObject } from '../App'
import { useHistory } from "react-router-dom";
import { render } from '@testing-library/react';

function CreateAccount() {
    const atmObject = useContext(AtmObject);
    const { accounts, setAccounts} = atmObject

    const history = useHistory();

    const initialValues = {
        username: "", 
        name: '',  
        email:"", 
        password: "",
        verifyPassword: ""
    }

    const onSubmit = (values)=>{
        console.log('values:',values)
        setAccounts(accounts,accounts[values.username] = {email: values.email, name: values.name, password:values.password, accountBalance: 0, accountHistory:[]});
        history.push('/components/Success')     
    }

    const validate = (values)=>{
        let errors = {};
        if(!values.username) {
            errors.username = 'Required'
        } 
        if(!values.name) {
            errors.name = 'Required'
        } 
        if(!values.email) {
            errors.email = 'Required'
        }else if (!/^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[A-Za-z]{2,4}$/.test(values.email)) { 
             errors.email = "Not a valid email"
        } 
        if(!values.password) {
            errors.password = 'Required'
        }else if(values.password.length < 8) {
            errors.password = "Password must be 8 or More Characters"
        }
        if(values.password != values.verifyPassword){
            errors.password = "Passwords don't match!"
            errors.verifyPassword = "Passwords don't match!"
        } 
        return errors
    }

    return (
        <div id="signup-form">
            <Formik
                initialValues = {initialValues}
                onSubmit = {onSubmit}
                validate = {validate}
                >
                <div id="sign-up">
                    <h2>Create Account</h2>
                    <Form><div className= "field-div">
                            <Field name="username" type='input' placeholder="Create Username"></Field>
                            <div className= "error"><ErrorMessage name = 'username'/></div>
                        </div>
                        <div className= "field-div">
                            <Field name="name" type='input' placeholder="Greeting Name"></Field>
                            <div className= "error"><ErrorMessage name = 'name'/></div>
                        </div>
                        <div className= "field-div">
                            <Field name="email" type='email' placeholder="Enter Email"></Field>
                            <div className= "error"><ErrorMessage name = 'email'/></div>
                        </div>
                        <div className= "field-div">
                            <Field name="password" type='input' placeholder="Create Password"></Field>
                            <div className= "error"><ErrorMessage name = 'password'/></div>
                        </div>
                        <div className= "field-div">
                            <Field name="verifyPassword" type='input' placeholder="Verify Password"></Field>
                            <div className= "error"><ErrorMessage name = 'verifyPassword'/></div>
                        </div>
                        <button type="submit" >Submit</button>
                    </Form>
                </div>
            </Formik>
        </div>
    )
}

export default CreateAccount
