import React from 'react'
import HandleHistory from './HandleHistory'

function AccountHistory() {
    return (
        <div key = 'history' id = 'history'>
            <h3 id = "history-title">Account History</h3>
            <ul id="history-list">
                { <HandleHistory />}
            </ul>
        </div>
    )
}

export default AccountHistory
