import React, { useState, useContext, useEffect } from 'react'
import Nav from 'react-bootstrap/Nav'
import { AtmObject } from '../App'
import '../App.css'
import { Link } from "react-router-dom";

function Navbar() {
    const atmObject = useContext(AtmObject)
    const { isVerified } = atmObject
    const [selected, setSelected] = useState('Home')

    const handleNav = (e)=>{
        let item = e.target.innerHTML
        if(document.getElementById(selected)){
        document.getElementById(selected).classList.remove('marble')
        document.getElementById(item).classList.add('marble')
        document.getElementById(selected).classList.remove('black-border-bottom')
        document.getElementById(item).classList.add('black-border-bottom')
        setSelected(item)
        }else{
            setSelected('Home')
        }
    } 
    
    useEffect(()=>{
        document.getElementById('Home').classList.add('marble')
    },[])

    return (
        <div id="navbar-div">
            <Nav id = "nav-component" fill variant="tabs" defaultActiveKey="/home">
                <Nav.Item>
                    <Link to="/components/Home" onClick = {handleNav} ><div className="nav-title" id="Home">Home</div></Link>
                </Nav.Item>
            {!isVerified &&
                <Nav.Item>
                    <Link to="/components/CreateAccount" onClick = {handleNav}><div className="nav-title" id="Create Account">Create Account</div></Link>
                </Nav.Item>
            }
            { isVerified &&
            <>
                <Nav.Item>
                    <Link to="/components/Withdraw" onClick = {handleNav}><div className="nav-title" id="Withdraw">Withdraw</div></Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="/components/Deposit" onClick = {handleNav}><div className="nav-title" id="Deposit">Deposit</div></Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="/components/AccountHistory" onClick = {handleNav}><div className="nav-title" id="Account History">Account History</div></Link>
                </Nav.Item>
                <Nav.Item>
                    <Link to="/components/Logout" onClick = {handleNav}><div className="nav-title" id="Logout">Logout</div></Link>
                </Nav.Item>
                
                </>
            }
            </Nav>
        </div>
    )
}

export default Navbar
